
rect.svgbar {
  fill: lightgrey;
  stroke: grey;
  stroke-opacity: 0.5;
  fill-opacity: 0.5;
}
rect.svgbar-interval {
  fill: red;
  opacity: .25;
  -webkit-transition: fill 2s ease-out;
  transition: fill 2s ease-out;
}
line.svgbar-value {
  stroke: red;
}
.svg-container {
  width: 100%;
  height: 0;
  padding: 0;
  /* reset */
  padding-bottom: 10%;
  position: relative;
}
svg {
  position: absolute;
  top: 0;
  left: 0;
  max-height: 2.4rem;
}



