.progress {
  margin-top: -3px;
  z-index: 999999;
  background-color: #33cccc;
  height: 3px;
  top: 0;
  left: 0;
  -webkit-transition: width 0.3s, opacity 0.3s;
  transition: width 0.3s, opacity 0.3s;
  -webkit-box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.4);
          box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.4);
}



