.notification {
  position: fixed;
  top: 50px;
  right: 0;
  z-index: 999;
  padding: 1rem;
}
.notification .item {
    padding: 1rem;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    background: rgba(211, 211, 211, 0.7);
    border-color: color;
    border-radius: 5px;
    -webkit-box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.3), 1px 1px 8px rgba(0, 0, 0, 0.3) inset;
            box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.3), 1px 1px 8px rgba(0, 0, 0, 0.3) inset;
    width: 400px;
}
.notification .item.success {
      background: rgba(51, 204, 51, 0.7);
      border-color: color;
}
.notification .item.warning {
      background: rgba(255, 153, 51, 0.7);
      border-color: color;
}
.notification .item.alert {
      background: rgba(255, 51, 51, 0.7);
      border-color: color;
}
.notification .item.primary {
      background: rgba(51, 204, 204, 0.7);
      border-color: color;
}
.notification .item .btn-clear {
      position: relative;
      background: transparent;
      border: 0;
      right: -0.2rem;
      top: -1.2rem;
      color: #404040;
      opacity: .8;
      font-size: 20px;
      text-decoration: none;
      float: right;
      cursor: pointer;
}
.notification .item .btn-clear:hover {
        opacity: 1;
}



