@media (min-width: 801px) {
.desktop-only {
    display: inline-block !important;
}
.mobile-only {
    display: none !important;
}
}
@media (max-width: 800px) {
.mobile-only {
    display: inline-block !important;
}
.desktop-only {
    display: none !important;
}
}
.nprogress-container {
  position: fixed !important;
  width: 100%;
  height: 50px;
  z-index: 2048;
  pointer-events: none;
}
.nprogress-container #nprogress .bar {
    background: #33cccc;
}
.nprogress-container #nprogress .peg {
    -webkit-box-shadow: 0 0 10px #33cccc, 0 0 5px #33cccc;
            box-shadow: 0 0 10px #33cccc, 0 0 5px #33cccc;
}
.nprogress-container #nprogress .spinner-icon {
    border-top-color: #33cccc;
    border-left-color: #33cccc;
}
#topbar {
  width: 100%;
  height: 42px;
  background-color: white;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.1);
  margin-left: 220px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
#topbar img {
    width: 35px;
    float: left;
    margin-left: 1rem;
}
#topbar h3 {
    margin: 0;
    margin-left: 5rem;
    line-height: 35px;
}

/* The side navigation menu */
#sidenav {
  height: 100%;
  /* 100% Full-height */
  width: 220px;
  /* 0 width - change this with JavaScript */
  position: fixed;
  /* Stay in place */
  z-index: 3;
  /* Stay on top */
  top: 0px;
  left: 0;
  background-color: white;
  /* Black*/
  overflow-x: hidden;
  /* Disable horizontal scroll */
  -webkit-transition: 0.5s;
  transition: 0.5s;
  /* 0.5 second transition effect to slide in the sidenav */
  -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.1);
}

/* The navigation menu links */
/* .sidenav a {
      padding: 8px 8px 8px 32px;
      text-decoration: none;
      font-size: 25px;
      color: #818181;
      display: block;
      transition: 0.3s
  } */
/* When you mouse over the navigation links, change their color */
