/* $breakpoints: (
  'xsmall' : ( min-width:  567px ),
  'small'  : ( min-width:  767px ),
  'medium' : ( min-width:  991px ),
  'large'  : ( min-width: 1199px )
); */
table {
  border-spacing: 0;
  font-size: 90%;
}
table thead {
    display: none;
}
table tbody td {
    display: block;
    padding: .5rem;
    text-align: left;
    background-color: white;
}
table tbody tr td:first-child {
    background: #e6e6e6;
}
table tbody td:before {
    content: attr(data-th) ": ";
    font-weight: bold;
    display: inline-block;
    width: 11rem;
}
table th {
    padding: 0.5rem;
}
@media screen and (min-width: 600px) {
table thead {
    display: table-header-group;
    background-image: -webkit-gradient(linear, left top, left bottom, from(whitesmoke), to(lightgray));
    background-image: linear-gradient(whitesmoke, lightgray);
}
table tbody td {
    display: table-cell;
    padding: 0.5rem;
    text-align: center;
}
table tbody td:before {
      display: none;
}
table tbody tr td:first-child {
    background-color: white;
}
table tbody tr:nth-child(2n-1) td {
    background-color: #e6e6e6;
}
}
table th span {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 6px solid gray;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
table th.active span.dsc {
  border-top: 6px solid #33cccc;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
}
table th.active span.asc {
  border-top: 6px solid #33cccc;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
table.rounded {
  border-collapse: separate;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 0 0 1px rgba(0, 0, 0, 0.1);
}
table.rounded thead th:first-child {
    border-radius: 5px 0 0 0;
}
table.rounded thead th:last-child {
    border-radius: 0 5px 0 0;
}
table.rounded tbody tr:last-child td:first-child {
    border-radius: 0 0 0 5px;
}
table.rounded tbody tr:last-child td:last-child {
    border-radius: 0 0 5px 0;
}
table.borderless {
  border: none;
}
table.borderless tr td {
    border-left: 0;
}

/* $breakpoints: (
  'xsmall' : ( min-width:  567px ),
  'small'  : ( min-width:  767px ),
  'medium' : ( min-width:  991px ),
  'large'  : ( min-width: 1199px )
); */
.has-tooltip {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  position: relative;
  /* &.small {
    white-space: normal;
    width: 100px;
  } */
}
.has-tooltip:after, .has-tooltip:before {
    opacity: 0;
    visibility: hidden;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
    z-index: 10;
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
}
.has-tooltip:after {
    -webkit-box-flex: 1;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
    background: rgba(64, 64, 64, 0.8);
    border-radius: 5px;
    bottom: 120%;
    color: #fff;
    content: attr(data-tooltip);
    left: -30%;
    padding: 0.5rem 1rem;
    position: absolute;
    display: block;
    white-space: normal;
}
.has-tooltip:before {
    border: solid;
    border-color: rgba(64, 64, 64, 0.8) transparent;
    border-width: 6px 6px 0 6px;
    bottom: 100%;
    content: "";
    left: 40%;
    position: absolute;
}
.has-tooltip:hover:after, .has-tooltip:hover:before {
    visibility: visible;
    opacity: 1;
}



