.search-select .menu {
  background-color: white;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border: 1px solid lightgray;
  border-top: 0;
  overflow-y: scroll;
  height: 20rem;
  width: 100%;
  position: absolute;
  z-index: 1000;
  margin-top: -5px;
}
.search-select .menu:hover, .search-select .menu.active {
  border: 1px solid #33cccc;
  border-top: 0;
}
.search-select .menu > .item:hover {
  background: none transparent !important;
}
.search-select .menu > .item.current {
  background: rgba(0, 0, 0, 0.2) !important;
}



