.modal-overlay {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.8);
  -webkit-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease;
}
.modal {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  overflow: hidden;
  position: fixed;
  z-index: 20;
}
.modal-content {
  background-color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: relative;
  margin: 0 auto;
  max-height: calc(100vh - 40px);
  overflow: hidden;
  width: 100%;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}
@media (min-width: 801px) {
.modal-content {
    border-radius: 5px;
    margin: 5% auto;
    max-height: calc(100vh - 150px);
    width: 70%;
}
}
.modal-body {
  -webkit-overflow-scrolling: touch;
  background-color: white;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  -ms-flex-negative: 1;
      flex-shrink: 1;
  overflow: auto;
  padding: 1rem;
}
.modal-footer {
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: end;
}
.modal-header {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.modal-header, .modal-footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding: 1rem;
}
.modal-header h4 {
  margin-bottom: 0;
}
.modal-header > * {
  width: 100%;
}
.modal-footer > button:first-child {
  margin-left: auto;
}
.modal-footer > button:not(:last-child) {
  margin-right: 10px;
}
.modal-close {
  position: absolute;
  right: 1rem;
  top: 1rem;
  background-color: gray;
  color: white;
  font-size: 3rem;
  width: 3rem;
  height: 3rem;
  text-align: center;
  line-height: 3rem;
  cursor: pointer;
  z-index: 1;
  border-radius: 50%;
}
.modal-close::hover {
    background-color: lightgray;
    color: white;
}
.modal .medium {
  width: 55%;
}
.modal .small {
  width: 40%;
}
.modal-enter-active, .modal-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.modal-enter, .modal-leave-to {
  opacity: 0;
}



